import { useState, useEffect } from "react";
import { Navbar, Nav, Container, NavDropdown, Offcanvas } from "react-bootstrap";
import logo from '../logos/svg/logo-no-background.svg';
import navIcon1 from '../images/nav-icon1.svg';
import navIcon2 from '../images/nav-icon2.svg';
import navIcon3 from '../images/nav-icon3.svg';
import './NavBar.css'
import { HashLink } from 'react-router-hash-link';
import { Link } from "react-router-dom";
import {FaBars} from 'react-icons/fa'
import {
  BrowserRouter as Router
} from "react-router-dom";

export const NavBar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [activeLink, setActiveLink] = useState('home');
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    }

    window.addEventListener("scroll", onScroll);

    return () => window.removeEventListener("scroll", onScroll);
  }, [])

  const handleScrollToSection = (sectionId) => {
    // setActiveLink(sectionId); // Uncomment this line if needed
    setIsMenuOpen(false); // Close menu on any link press if mobile
    const section = document.getElementById(sectionId);
    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <Router>
     <Navbar expand="md" className={scrolled ? "scrolled" : ""}>
        <Container>
          <Navbar.Brand href="/">
            <img src={logo} alt="Logo" />
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav">
            <span className="navbar-toggler-icon"></span>
          </Navbar.Toggle>
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
            <Nav.Link as={Link} to="/" className='navbar-link' onClick={() => handleScrollToSection('about')}>
              About
            </Nav.Link>
            <Nav.Link as={Link} className='navbar-link' to="/recipes">
              Recipes
            </Nav.Link>
            <NavDropdown title="Services" id="basic-nav-dropdown">
              <NavDropdown.Item as={Link} className='navbar-link' to="/services/weight-management">
                Weight Management
              </NavDropdown.Item>
              <NavDropdown.Item as={Link} className='navbar-link' to="/services/sports-nutrition">
                Sports Nutrition
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link as={Link} to="/" className='navbar-link' onClick={() => handleScrollToSection('connect')}>
              Contact Us
            </Nav.Link>
            </Nav>
            <span className="navbar-text">
              <div className="social-icon">
                <a href="#"><img src={navIcon1} alt="" /></a>
                <a href="#"><img src={navIcon2} alt="" /></a>
                <a href="#"><img src={navIcon3} alt="" /></a>
              </div>
              <Nav.Link as = {Link} to='/SignIn'>
                <button className="vvd"><span>Let’s Connect</span></button>
              </Nav.Link>
            </span>
          </Navbar.Collapse>

        </Container>
      </Navbar>
  </Router>
  )
}
